import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Person4Icon from '@mui/icons-material/Person4';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';
import BadgeIcon from '@mui/icons-material/Badge';
import Divider from '@mui/material/Divider';

import {
  DateField,
  Loader,
  SearchField,
  SelectField,
  SelectMultiField,
  TextField,
} from '../components';

import {
  OwnersQuery,
  CoordinatorsQuery,
  DriversQuery,
  TruckCreate,
} from '../api';
import { useSnackbar } from '../hooks';
import {
  Owner,
  Coordinator,
  Driver,
  CreateTruck,
  TruckCertificates,
  TruckCrossborders,
  TruckEquipments,
  TruckStatuses,
  TruckTypes,
  TruckStatus,
  TruckCrossborder,
  TruckCertificate,
  TruckType,
} from '../utils/dataTypes';
import { getType } from '../utils/queryHelpers';

const validationSchema = yup.object({
  truckNumber: yup
    .number()
    .integer()
    .typeError('Truck Number must be a integer number')
    .required('Truck Number is required'),
  status: yup.string().oneOf(TruckStatuses).required('Status is required'),
  crossborder: yup
    .string()
    .oneOf(TruckCrossborders)
    .required('Crossborder is required'),
  certificate: yup
    .string()
    .oneOf(TruckCertificates)
    .required('Certificate is required'),
  type: yup.string().oneOf(TruckTypes).required('Type is required'),
  equipment: yup
    .array(yup.string().oneOf(TruckEquipments))
    .min(0)
    .required('Equipment is required'),
  payload: yup
    .number()
    .integer()
    .typeError('Payload must be a integer number')
    .required('Payload is required'),
  grossWeight: yup.string().required('Gross Weight is required'),
  make: yup.string().required('Make is required'),
  model: yup.string().required('Model is required'),
  year: yup
    .number()
    .integer()
    .typeError('Year must be a integer number')
    .required('Year is required'),
  color: yup.string().required('Color is required'),
  vinCode: yup.string().required('Vin Code is required'),
  licencePlate: yup.string().required('Licence Plate is required'),
  licenceState: yup.string().required('Licence State is required'),
  plateExpires: yup
    .date()
    .typeError('Enter correct date')
    .required('Plate Expires is required'),
  insideDims: yup.string().required('Inside Dims is required'),
  doorDims: yup.string().required('Door Dims is required'),
  owner: yup
    .object({ id: yup.string().required() })
    .required('Owner is required'),
  coordinator: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct coordinator')
    .nullable(),
  driver: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct driver')
    .nullable(),
});

const initialValues = {
  truckNumber: undefined,
  status: 'Available' as TruckStatus,
  crossborder: 'Yes' as TruckCrossborder,
  certificate: 'Hazmat' as TruckCertificate,
  type: 'Cargo van' as TruckType,
  equipment: [],
  payload: undefined,
  grossWeight: undefined,
  make: undefined,
  model: undefined,
  year: undefined,
  color: undefined,
  vinCode: undefined,
  licencePlate: undefined,
  licenceState: undefined,
  plateExpires: undefined,
  insideDims: undefined,
  doorDims: undefined,
  owner: null,
  coordinator: null,
  driver: null,
};

export default function CreateTruckForm() {
  const [driverAddQueryParams, setDriverAddQueryParams] = React.useState<
    Record<string, string>
  >({});
  const [coordinatorAddQueryParams, setCoordinatorAddQueryParams] =
    React.useState<Record<string, string>>({});
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(TruckCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['trucks']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateTruck>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateTruck);
    },
  });

  React.useEffect(() => {
    if (formik.values.owner) {
      setDriverAddQueryParams({ owner: formik.values.owner.id });
      setCoordinatorAddQueryParams({ owner: formik.values.owner.id });
    } else {
      formik.setFieldValue('driver', null, true);
      formik.setFieldValue('coordinator', null, true);
      setDriverAddQueryParams((prev) => (Object.keys(prev).length ? {} : prev));
      setCoordinatorAddQueryParams((prev) =>
        Object.keys(prev).length ? {} : prev,
      );
    }
  }, [formik.values.owner]);

  React.useEffect(() => {
    if (
      formik.values.coordinator &&
      formik.values.coordinator.owner &&
      formik.values.coordinator.id !==
        (formik.values.owner && formik.values.owner.id)
    ) {
      formik.setFieldValue('owner', formik.values.coordinator.owner, true);
    }
  }, [formik.values.coordinator]);

  React.useEffect(() => {
    if (
      formik.values.driver &&
      formik.values.driver.owner &&
      formik.values.driver.id !==
        (formik.values.owner && formik.values.owner.id)
    ) {
      formik.setFieldValue('owner', formik.values.driver.owner, true);
    }
  }, [formik.values.driver]);

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, display: 'grid', height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              height: '100%',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, paddingBottom: 0, width: '50%' }}>
                <TextField
                  fieldName="truckNumber"
                  label="Truck Number"
                  number
                  formik={formik}
                />
                <SelectField
                  fieldName="crossborder"
                  label="Cross Border"
                  items={TruckCrossborders}
                  formik={formik}
                />
                <SelectField
                  fieldName="type"
                  label="Type"
                  items={TruckTypes}
                  formik={formik}
                />
                <TextField
                  fieldName="payload"
                  label="Payload"
                  number
                  formik={formik}
                />
                <TextField fieldName="make" label="Make" formik={formik} />
                <TextField
                  fieldName="year"
                  label="Year"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="vinCode"
                  label="Vin Code"
                  formik={formik}
                />
                <TextField
                  fieldName="licenceState"
                  label="Licence State"
                  formik={formik}
                />
                <TextField
                  fieldName="insideDims"
                  label="Inside Dims (length x width x height)"
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, paddingBottom: 0, width: '50%' }}>
                <SelectField
                  fieldName="status"
                  label="Status"
                  items={TruckStatuses}
                  formik={formik}
                />
                <SelectField
                  fieldName="certificate"
                  label="Certificate"
                  items={TruckCertificates}
                  formik={formik}
                />
                <SelectMultiField
                  fieldName="equipment"
                  label="Equipment"
                  items={TruckEquipments}
                  formik={formik}
                />
                <TextField
                  fieldName="grossWeight"
                  label="Gross Weight"
                  formik={formik}
                />
                <TextField fieldName="model" label="Model" formik={formik} />
                <TextField fieldName="color" label="Color" formik={formik} />
                <TextField
                  fieldName="licencePlate"
                  label="Licence Plate"
                  formik={formik}
                />
                <DateField
                  fieldName="plateExpires"
                  label="Plate Expires"
                  formik={formik}
                />
                <TextField
                  fieldName="doorDims"
                  label="Door Dims (width x height)"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <SearchField<Owner>
                fieldName="owner"
                label="Owner"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={Person4Icon}
                queryFn={OwnersQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                formik={formik}
              />
              <SearchField<Coordinator>
                fieldName="coordinator"
                label="Coordinator"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={ContactPhoneIcon}
                queryFn={CoordinatorsQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                additionalQueryParams={coordinatorAddQueryParams}
                formik={formik}
              />
              <SearchField<Driver>
                fieldName="driver"
                label="Driver"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) =>
                  `${item.fullName}${
                    item.driveTrucks && item.driveTrucks[0]
                      ? ', On Truck: ' + item.driveTrucks[0].truckNumber
                      : ''
                  }`
                }
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                getOptionDisabled={(item) => !!item.driveTrucks?.length}
                ListIcon={BadgeIcon}
                queryFn={DriversQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                additionalQueryParams={driverAddQueryParams}
                formik={formik}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
